import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function evToFreeCashFlowTtm(tableName: string) {
  const title = "EV/FCF (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["evToFreeCashFlowTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "EV vs. free cash flow.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about enterprise value to free cash flow or other relevant financial measures?`,
              value: info.row.original.financialData.companyKeyMetrics?.data?.evToFreeCashFlowTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.evToFreeCashFlowTtm}
        />
      )
    },
  })
}

export { evToFreeCashFlowTtm }
